import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {IndexPage} from './index.page';
import {LoginGuard, PreLogin} from '../shared-components/authentication/auth-guards.service';

const routes: Routes = [
    {
        path: '',
        component: IndexPage,
        children: [
            {
                path: '',
                canActivate: [PreLogin],
                loadChildren: () => import('../pages/app-slides/slides.module').then((e) => e.SlidesModule)
            }, {
                path: 'login',
                canActivate: [PreLogin],
                loadChildren: () => import('../pages/login/login.module').then((e) => e.LoginPageModule)
            }, {
                path: 'sign-up',
                canActivate: [LoginGuard],
                loadChildren: () => import('../pages/signup/signup.module').then((e) => e.SignupPageModule)
            }
        ]
    }

];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class IndexRouter {
}
